#content-area{
    margin-left: 280px;
    padding: 15px;
    .heading{
        h1{ 
            color: blueviolet;
            font-size: 1.4rem;
            font-weight: 400;
            margin-bottom: 10px;
        }
        p{
            color: rgb(75, 72, 72);
            font-size: 0.8rem;
        }
    }
}




.cards{
    display: inline-flex;
    overflow: auto;
    margin-bottom: 30px;

    .card{
        background: #fff;
        border-radius: 5px;
        padding: 20px;
        margin: 10px;
        overflow: auto;


        .user-img{
            background: url("https://images.unsplash.com/photo-1519699047748-de8e457a634e?ixlib=rb-1.2.1&q=80&fm=jpg&crop=faces&fit=crop&h=200&w=200&ixid=eyJhcHBfaWQiOjE3Nzg0fQ") no-repeat center center; 
            -webkit-background-size: cover;
            -moz-background-size: cover;
            -o-background-size: cover;
            background-size: cover;
            width: 115px;
            height: 115px;
            border-radius: 50%;
            margin: 0 auto;
        }

        .user-name{
            font-size: 1rem;
            color: purple;
            display: block;
            text-align: center;
            margin: 10px 0;
        }

        .user-title{
            display: block;
            font-size: 0.8rem;
            text-align: center;
            margin: 10px 0;
        }

        .education{
            font-size: 0.8rem;
            display: inline-block;
            color: lightgray;
        }

        .schools{
            font-size: 0.8rem;
        }

        h6{
            font-size: 0.7rem;
            color:rgb(110, 107, 107);
        }

        .date{
            font-size: 1.3rem;
            display: block;
            border-left: 5px solid greenyellow;
            position: relative;
            left: -20px;
            padding: 5px 40px;
            margin: 20px 0;
        }

        .job-type{
            background-color: orange;
            width: 20px;
            height:20px;
            font-size: 0.6rem;
            border-radius: 50%;
            color:white;
            // padding: 5px;
            display: inline-block;
            text-align: center;
            vertical-align: middle;
        }

        .job-type-os{
            background-color: lightskyblue;
            width: 20px;
            height:20px;
            font-size: 0.6rem;
            border-radius: 50%;
            color:white;
            // padding: 5px;
            display: inline-block;
            text-align: center;
            vertical-align: middle;
        }

        .location{
            font-size: 0.8rem;
            font-weight: 300;
            display: block;
            margin-bottom: 0.1rem;
        }

        .position{
            font-size: 0.8rem;
            font-weight: 500;
            display: block;
            margin-bottom: 0.1rem;
        }

        .time{
            font-size: 0.8rem;
            font-weight: 300;
            display: block;
            color:rgb(110, 107, 107);
            margin-bottom: 0.1rem;
        }
    }
}
